import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { countries } from "@/data/counties";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getClientTypes } from "@/lib/api/clientApi";
import { getCurrencies } from "@/lib/api/currencyApi";
import { getUsers } from "@/lib/api/userApi";
import { Client, ClientFilters, ClientSort, ClientType } from "@/types/client";
import { Currency } from "@/types/currency";
import { User } from "@/types/user";
import { Filter, Search } from "lucide-react";
import { useEffect, useState } from "react";
import ClientTable from "./client-table";

interface ClientTableContainerProps {
  handleEditClient: (client: Client) => void;
  handleDeleteClient: (clientId: string) => void;
  clients: Client[];
  setFilteredClients: (clients: Client[]) => void;
  filteredClients: Client[];
  handleViewDetails: (client: Client) => void;
}

const ClientTableContainer = ({
  handleEditClient,
  handleDeleteClient,
  clients,
  setFilteredClients,
  filteredClients,
  handleViewDetails,
}: ClientTableContainerProps) => {
  const { data: marketers } = useSkillQuery<User[]>({
    queryKey: ["users"],
    queryFn: () => getUsers(),
  });
  const { data: currencies } = useSkillQuery<Currency[]>({
    queryKey: ["currencies"],
    queryFn: () => getCurrencies(),
  });
  const { data: clientTypes } = useSkillQuery<ClientType[]>({
    queryKey: ["clientTypes"],
    queryFn: () => getClientTypes(),
  });

  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(10);

  const [filters, setFilters] = useState<ClientFilters>({
    search: "",
    sg: "",
    currency: "",
    country: "",
    marketer: "",
    type: "",
    deadline: "",
  });

  const [sort, setSort] = useState<ClientSort>({
    field: "name",
    direction: "asc",
  });

  // Apply filters and sorting
  const applyFiltersAndSort = () => {
    let filtered = [...clients];

    // Apply filters
    if (filters.search) {
      const searchLower = filters.search.toLowerCase();
      filtered = filtered.filter(
        (client) =>
          client.name.toLowerCase().includes(searchLower) ||
          client.contactPerson?.toLowerCase().includes(searchLower) ||
          client.email?.toLowerCase().includes(searchLower) ||
          client.companyName?.toLowerCase().includes(searchLower)
      );
    }

    if (filters.sg) {
      filtered = filtered.filter((client) =>
        client.sgCode.toLowerCase().includes(filters.sg.toLowerCase())
      );
    }

    if (filters.currency) {
      filtered = filtered.filter(
        (client) => client.currencyId === filters.currency
      );
    }

    if (filters.country) {
      filtered = filtered.filter(
        (client) => client.country === filters.country
      );
    }

    if (filters.marketer) {
      filtered = filtered.filter(
        (client) => client.marketerId === filters.marketer
      );
    }

    if (filters.type) {
      filtered = filtered.filter(
        (client) => client.clientTypeId === filters.type
      );
    }

    // Apply sorting
    filtered.sort((a, b) => {
      const aValue = a[sort.field] || "";
      const bValue = b[sort.field] || "";

      if (typeof aValue === "string" && typeof bValue === "string") {
        return sort.direction === "asc"
          ? aValue.localeCompare(bValue)
          : bValue.localeCompare(aValue);
      }

      if (typeof aValue === "number" && typeof bValue === "number") {
        return sort.direction === "asc" ? aValue - bValue : bValue - aValue;
      }

      return 0;
    });

    setFilteredClients(filtered);
    setCurrentPage(1);
  };

  // Apply filters whenever they change
  useEffect(() => {
    applyFiltersAndSort();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clients, filters, sort]);

  const handleFilterChange = (key: keyof ClientFilters, value: string) => {
    if (value === "all") {
      setFilters((prev) => ({ ...prev, [key]: "" }));
      return;
    }
    setFilters((prev) => ({ ...prev, [key]: value }));
  };

  // Handle sort changes
  const handleSort = (field: keyof Client) => {
    setSort((prev) => ({
      field,
      direction:
        prev.field === field && prev.direction === "asc" ? "desc" : "asc",
    }));
  };

  const clearFilters = () => {
    setFilters({
      search: "",
      sg: "",
      currency: "",
      country: "",
      marketer: "",
      type: "",
      deadline: "",
    });
    setCurrentPage(1);
  };

  // Pagination
  const totalPages = Math.ceil(filteredClients.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const paginatedClients = filteredClients.slice(
    startIndex,
    startIndex + itemsPerPage
  );

  return (
    <>
      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Filter className="h-5 w-5" />
            Filters
          </CardTitle>
          <CardDescription>Filter and search clients</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div className="relative">
              <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search clients..."
                value={filters.search}
                onChange={(e) => handleFilterChange("search", e.target.value)}
                className="pl-10"
              />
            </div>

            <Input
              placeholder="Filter by SG code..."
              value={filters.sg}
              onChange={(e) => handleFilterChange("sg", e.target.value)}
            />

            <Select
              value={filters.currency}
              onValueChange={(value) => handleFilterChange("currency", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by currency" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Currencies</SelectItem>
                {currencies?.data.map((currency) => (
                  <SelectItem key={currency.id} value={currency.id}>
                    {currency.symbol} {currency.code}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              value={filters.country}
              onValueChange={(value) => handleFilterChange("country", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by country" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Countries</SelectItem>
                {countries.map((country) => (
                  <SelectItem
                    key={country.name.common}
                    value={country.name.common}
                  >
                    {country.name.common}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              value={filters.marketer}
              onValueChange={(value) => handleFilterChange("marketer", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by marketer" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Marketers</SelectItem>
                {marketers?.data.map((marketer) => (
                  <SelectItem key={marketer.id} value={marketer.id}>
                    {marketer.fullName}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              value={filters.type}
              onValueChange={(value) => handleFilterChange("type", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Types</SelectItem>
                {clientTypes?.data.map((type) => (
                  <SelectItem key={type.id} value={type.id}>
                    {type.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            {/* <Select
              value={filters.deadline}
              onValueChange={(value) => handleFilterChange("deadline", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by deadline" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Deadlines</SelectItem>
                {deadlines.map((deadline) => (
                  <SelectItem key={deadline} value={deadline}>
                    {deadline}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select> */}

            <div className="flex items-end">
              <Button
                variant="outline"
                onClick={clearFilters}
                className="w-full bg-transparent"
              >
                Clear Filters
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Client Table */}
      <ClientTable
        clients={paginatedClients}
        sort={sort}
        onSort={handleSort}
        onEdit={handleEditClient}
        onDelete={handleDeleteClient}
        currentPage={currentPage}
        totalPages={totalPages}
        onPageChange={setCurrentPage}
        totalItems={filteredClients.length}
        itemsPerPage={itemsPerPage}
        onViewDetails={handleViewDetails}
      />
    </>
  );
};
export default ClientTableContainer;
