"use client";

import ErrorPage from "@/components/error-page";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { useSkillMutation } from "@/hooks/useSkillMutation";
import useSkillQuery from "@/hooks/useSkillQuery";
import { deleteClient, getClients } from "@/lib/api/clientApi";
import type { Client } from "@/types/client";
import { Download, Plus } from "lucide-react";
import { useState } from "react";
import { toast } from "sonner";
import ClientDetailsModal from "./client-details-modal";
import ClientDialog from "./client-dialog";
import ClientTableContainer from "./client-table-container";

const ClientManagement = () => {
  // #region API Calls
  const { data: clients, isFetching } = useSkillQuery<Client[]>({
    queryKey: ["clients"],
    queryFn: () => getClients(),
  });

  const { mutateAsync: deleteClientMutation } = useSkillMutation<
    string,
    string
  >({
    mutationFn: (clientId: string) => deleteClient(clientId),
    queryKey: ["clients"],
  });
  // #endregion

  // #region State
  const [filteredClients, setFilteredClients] = useState<Client[]>(
    clients?.data || []
  );
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingClient, setEditingClient] = useState<Client | null>(null);
  const [isDetailsModalOpen, setIsDetailsModalOpen] = useState(false);
  const [selectedClient, setSelectedClient] = useState<Client | null>(null);

  // Handle filter changes

  // Handle client operations
  const handleAddClient = () => {
    setEditingClient(null);
    setIsDialogOpen(true);
  };

  const handleEditClient = (client: Client) => {
    setEditingClient(client);
    setIsDialogOpen(true);
  };

  const handleViewDetails = (client: Client) => {
    setSelectedClient(client);
    setIsDetailsModalOpen(true);
  };

  const handleDeleteClient = async (clientId: string) => {
    const toastId = toast.loading("Deleting client...");

    const res = await deleteClientMutation(clientId);

    if (res.success) {
      toast.success("Client deleted successfully!", { id: toastId });
      return;
    }
    toast.dismiss(toastId);
  };

  if (!isFetching && !clients?.success)
    return (
      <ErrorPage
        status={500}
        message={clients?.message || "Something went wrong"}
      />
    );

  return (
    <div className="space-y-6">
      {/* Header Actions */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div className="flex items-center gap-2">
          <Badge variant="secondary" className="text-sm">
            {filteredClients.length} clients
          </Badge>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm">
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
          <Button onClick={handleAddClient} size="sm">
            <Plus className="h-4 w-4 mr-2" />
            Add Client
          </Button>
        </div>
      </div>

      <ClientTableContainer
        clients={clients?.data || []}
        setFilteredClients={setFilteredClients}
        filteredClients={filteredClients}
        handleEditClient={handleEditClient}
        handleDeleteClient={handleDeleteClient}
        handleViewDetails={handleViewDetails}
      />

      {/* Client Dialog */}
      <ClientDialog
        isOpen={isDialogOpen}
        onClose={() => setIsDialogOpen(false)}
        client={editingClient}
      />

      {/* Client Details Modal */}
      <ClientDetailsModal
        isOpen={isDetailsModalOpen}
        onClose={() => setIsDetailsModalOpen(false)}
        client={selectedClient}
      />
    </div>
  );
};

export default ClientManagement;
