import { z } from "zod";

export const clientSchema = z.object({
  sgCode: z.string().trim().min(1, "SG Code is required"),
  name: z.string().trim().min(1, "Name is required"),
  address: z.string().trim().min(1, "Address is required"),
  phone: z.string().trim().min(1, "Phone is required"),
  email: z.email("Invalid email"),
  companyName: z.string().trim().min(1, "Company Name is required"),
  contactPerson: z.string().trim().min(1, "Contact Person is required"),
  website: z.url("Invalid website URL"),

  country: z.string().trim().min(1, "Country is required"),
  anotherContactPerson: z.string().trim().optional(),
  anotherEmail: z.email("Invalid Another Email").optional(),
  vatId: z.string().trim().optional(),
  paymentCycle: z.number().min(0, "Payment cycle is required"),
  deadlineType: z.number().min(0, "Deadline type is required"),
  fileBackup: z.string().optional(),
  lockDate: z.date({ error: "Lock date is required" }),

  marketerId: z.string().min(1, "Marketer is required"),
  currencyId: z.string().min(1, "Currency is required"),
  paymentMethodId: z.string().min(1, "Payment method is required"),
  fileTransferMethodId: z.string().min(1, "File transfer method is required"),
  clientTypeId: z.string().min(1, "Client type is required"),
  folderId: z.string().optional(),
});

/** Type inferred from the zod schema */
export type ClientFormSchema = z.infer<typeof clientSchema>;

/**
 * Default values for client form based on the clientSchema
 * Provides sensible defaults for all fields in the schema
 */
export const defaultClientValues: ClientFormSchema = {
  sgCode: "",
  name: "",
  address: "",
  phone: "",
  email: "",
  companyName: "",
  contactPerson: "",
  website: "",
  country: "",
  marketerId: "",
  currencyId: "",
  paymentMethodId: "",
  clientTypeId: "",

  paymentCycle: 0,
  deadlineType: 0,

  anotherContactPerson: "",
  anotherEmail: "",
  vatId: "",
  fileBackup: "",
  fileTransferMethodId: "",
  folderId: "",

  lockDate: undefined as unknown as Date,
};
