"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import CustomRHFDateInput from "@/components/ui/CustomRHFDateInput";
import CustomRHFInput from "@/components/ui/CustomRHFInput";
import CustomRHFSelect from "@/components/ui/CustomRHFSelect";
import CustomRHFTextArea from "@/components/ui/CustomRHFTextArea";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { ScrollArea } from "@/components/ui/scroll-area";
import { SelectItem } from "@/components/ui/select";
import { countries } from "@/data/counties";
import { useSkillMutation } from "@/hooks/useSkillMutation";
import useSkillQuery from "@/hooks/useSkillQuery";
import {
  createClient,
  getClientTypes,
  updateClient,
} from "@/lib/api/clientApi";
import { getCurrencies } from "@/lib/api/currencyApi";
import { getFileTransferMethods } from "@/lib/api/fileTransferApi";
import { getDeadlines } from "@/lib/api/jobApi";
import { getPaymentCycles, getPaymentMethods } from "@/lib/api/paymentApi";
import { getUsers } from "@/lib/api/userApi";
import { toBangladeshTime } from "@/lib/timeHelper";
import {
  ClientFormSchema,
  clientSchema,
  defaultClientValues,
} from "@/lib/validation/client";
import type { Client } from "@/types/client";
import { zodResolver } from "@hookform/resolvers/zod";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { toast } from "sonner";

interface ClientDialogProps {
  isOpen: boolean;
  onClose: () => void;
  client?: Client | null;
}

const ClientDialog = ({ isOpen, onClose, client }: ClientDialogProps) => {
  // #region RHF
  const {
    register,
    setValue,
    handleSubmit,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<ClientFormSchema>({
    resolver: zodResolver(clientSchema),
  });

  // #region API Calls
  const { data: marketers } = useSkillQuery({
    queryKey: ["users"],
    queryFn: () => getUsers(),
  });

  const { data: clientTypes } = useSkillQuery({
    queryKey: ["clientTypes"],
    queryFn: () => getClientTypes(),
  });

  const { data: currencies } = useSkillQuery({
    queryKey: ["currencies"],
    queryFn: () => getCurrencies(),
  });

  const { data: paymentCycles } = useSkillQuery({
    queryKey: ["paymentCycles"],
    queryFn: () => getPaymentCycles(),
  });

  const { data: paymentMethods } = useSkillQuery({
    queryKey: ["paymentMethods"],
    queryFn: () => getPaymentMethods(),
  });

  const { data: fileTransferMethods } = useSkillQuery({
    queryKey: ["fileTransferMethods"],
    queryFn: () => getFileTransferMethods(),
  });

  const { data: deadlines } = useSkillQuery({
    queryKey: ["deadlines"],
    queryFn: () => getDeadlines(),
  });

  const { mutateAsync: updateClientFunc } = useSkillMutation<
    { clientId: string; client: ClientFormSchema },
    Client
  >({
    mutationFn: ({ client, clientId }) => updateClient({ client, clientId }),
    onClose,
    reset,
    queryKey: ["clients"],
  });

  const { mutateAsync: createClientFunc } = useSkillMutation<
    ClientFormSchema,
    Client
  >({
    mutationFn: (client) => createClient(client),
    onClose,
    reset,
    queryKey: ["clients"],
  });
  // #endregion

  // #region States and local functions
  const [lockDate, setLockDate] = useState<Date | undefined>(undefined);

  const handleDateChange = ({ field, date }: { field: string; date: Date }) => {
    if (field === "lockDate") setLockDate(date);
  };

  useEffect(() => {
    if (client) {
      reset(client);
      setLockDate(toBangladeshTime(client.lockDate));
      setValue("lockDate", toBangladeshTime(client.lockDate) || new Date());
    } else {
      setLockDate(undefined);
      reset(defaultClientValues);
    }
  }, [client, reset, setValue]);

  // #region Handlers
  const onSubmit = async (data: ClientFormSchema) => {
    const toastId = toast.loading("Saving...");
    try {
      if (client) {
        const res = await updateClientFunc({
          client: data,
          clientId: client?.id as string,
        });
        if (res.success)
          toast.success("Client updated successfully!", { id: toastId });
      } else {
        const res = await createClientFunc(data);
        if (res.success)
          toast.success("Client created successfully!", { id: toastId });
      }
    } catch (error) {
      console.error(error);
      toast.error("Error saving client: ", { id: toastId });
    }
  };

  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="min-w-[80vw] lg:min-w-5xl">
        <DialogHeader>
          <DialogTitle>{client ? "Edit Client" : "Add New Client"}</DialogTitle>
          <DialogDescription>
            {client ? "Update client information" : "Enter client details."}
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="max-h-[70vh]">
          {/* #region Form start */}
          <form>
            <Card>
              <CardContent className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="SG Code"
                    id="sgCode"
                  />

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Client Name"
                    id="name"
                  />
                </div>

                {/* Basic Information */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <CustomRHFSelect
                    errors={errors}
                    defaultValue={client?.currencyId}
                    label="Currency"
                    setValue={setValue}
                    id="currencyId"
                  >
                    {currencies?.data.map((currency) => (
                      <SelectItem key={currency.id} value={currency.id}>
                        {currency.symbol} - {currency.name}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Company Name"
                    id="companyName"
                  />

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Contact Person Name"
                    id="contactPerson"
                  />

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Phone Number"
                    id="phone"
                  />

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Email Address"
                    type="email"
                    id="email"
                  />

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Website"
                    placeholder="https://example.com"
                    id="website"
                  />
                </div>

                {/* Address */}
                <CustomRHFTextArea
                  register={register}
                  errors={errors}
                  label="Address"
                  id="address"
                />

                {/* Additional Contacts */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <CustomRHFSelect
                    errors={errors}
                    label="Marketer"
                    setValue={setValue}
                    defaultValue={client?.marketerId}
                    id="marketerId"
                  >
                    {marketers?.data.map((user) => (
                      <SelectItem key={user.id} value={user.id}>
                        {user.fullName}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFSelect
                    errors={errors}
                    label="Country"
                    setValue={setValue}
                    defaultValue={client?.country}
                    id="country"
                  >
                    {countries.map((country) => (
                      <SelectItem
                        key={country.name.common}
                        value={country.name.common}
                      >
                        {country.name.common}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Another Contact Person Name"
                    id="anotherContactPerson"
                  />

                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="Another Email"
                    id="anotherEmail"
                  />
                </div>

                {/* Business Information */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <CustomRHFInput
                    register={register}
                    errors={errors}
                    label="VAT/TAX ID"
                    id="vatId"
                  />

                  <CustomRHFSelect
                    errors={errors}
                    label="Payment Cycle"
                    setValue={setValue}
                    defaultValue={client?.paymentCycle?.toString()}
                    id="paymentCycle"
                    valueAsNumber={true}
                  >
                    {paymentCycles?.data.map((cycle) => (
                      <SelectItem
                        key={cycle.value}
                        value={cycle.value.toString()}
                      >
                        {cycle.name}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFSelect
                    errors={errors}
                    label="Payment Method"
                    setValue={setValue}
                    defaultValue={client?.paymentMethodId}
                    id="paymentMethodId"
                  >
                    {paymentMethods?.data.map((method) => (
                      <SelectItem key={method.id} value={method.id}>
                        {method.name}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFSelect
                    errors={errors}
                    label="File Transfer"
                    setValue={setValue}
                    defaultValue={client?.fileTransferMethodId}
                    id="fileTransferMethodId"
                  >
                    {fileTransferMethods?.data.map((method) => (
                      <SelectItem key={method.id} value={method.id}>
                        {method.name}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFSelect
                    errors={errors}
                    label="Deadline Type"
                    setValue={setValue}
                    defaultValue={client?.deadlineType?.toString()}
                    id="deadlineType"
                    valueAsNumber={true}
                  >
                    {deadlines?.data.map((type) => (
                      <SelectItem
                        key={type.value}
                        value={type.value.toString()}
                      >
                        {type.name}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>

                  <CustomRHFSelect
                    errors={errors}
                    label="Client Type"
                    setValue={setValue}
                    defaultValue={client?.clientTypeId}
                    id="clientTypeId"
                  >
                    {clientTypes?.data.map((type) => (
                      <SelectItem key={type.id} value={type.id}>
                        {type.name}
                      </SelectItem>
                    ))}
                  </CustomRHFSelect>
                </div>

                {/* Test Information */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="folderId">Folder ID</Label>
                    <Input
                      id="folderId"
                      {...register("folderId")}
                      placeholder="Enter folder ID"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="fileBackup">File Backup</Label>
                    <Input
                      id="fileBackup"
                      {...register("fileBackup")}
                      placeholder="Enter file backup method"
                    />
                  </div>

                  <CustomRHFDateInput
                    errors={errors}
                    label="Lock Date"
                    id="lockDate"
                    setValue={setValue}
                    dateValue={lockDate}
                    handleDateChange={handleDateChange}
                  />
                </div>
              </CardContent>
            </Card>
          </form>
        </ScrollArea>

        <DialogFooter>
          <Button type="button" variant="outline" onClick={handleClose}>
            Cancel
          </Button>
          <Button
            type="submit"
            disabled={isSubmitting}
            onClick={handleSubmit(onSubmit)}
          >
            {client ? "Update Client" : "Add Client"}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

export default ClientDialog;
