import type { Invoice, InvoiceSummary } from "../types/invoice";

export function calculateInvoiceRiskLevel(
  invoice: Invoice
): "completed" | "low" | "medium" | "high" | "critical" {
  if (invoice.status === "Paid") {
    return "completed";
  }

  const today = new Date();
  const dueDate = new Date(invoice.dueDate);
  const daysDiff = Math.ceil(
    (dueDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24)
  );

  if (daysDiff < 0) {
    return "critical"; // Overdue
  } else if (daysDiff <= 1) {
    return "high"; // Due within 1 day
  } else if (daysDiff <= 3) {
    return "medium"; // Due within 3 days
  } else if (daysDiff <= 7) {
    return "medium"; // Due within 7 days
  } else {
    return "low"; // More than 7 days
  }
}

export function getInvoiceStatusColor(status: Invoice["status"]): string {
  switch (status) {
    case "Paid":
      return "bg-green-100 text-green-800 border-green-200";
    case "Due":
      return "bg-blue-100 text-blue-800 border-blue-200";
    case "Overdue":
      return "bg-red-100 text-red-800 border-red-200";
    case "Partial Paid":
      return "bg-yellow-100 text-yellow-800 border-yellow-200";
    default:
      return "bg-gray-100 text-gray-800 border-gray-200";
  }
}

export function getRiskLevelColor(riskLevel: string): string {
  switch (riskLevel) {
    case "completed":
      return "bg-green-100 text-green-800 border-green-200";
    case "low":
      return "bg-blue-100 text-blue-800 border-blue-200";
    case "medium":
      return "bg-yellow-100 text-yellow-800 border-yellow-200";
    case "high":
      return "bg-orange-100 text-orange-800 border-orange-200";
    case "critical":
      return "bg-red-100 text-red-800 border-red-200";
    default:
      return "bg-gray-100 text-gray-800 border-gray-200";
  }
}

export function calculateInvoiceSummary(invoices: Invoice[]): InvoiceSummary {
  const totalInvoices = invoices.length;
  const totalRevenue = invoices.reduce(
    (sum, invoice) => sum + invoice.amount,
    0
  );
  const totalPaid = invoices.reduce(
    (sum, invoice) => sum + (invoice.paidAmount || 0),
    0
  );
  const outstandingAmount = totalRevenue - totalPaid;
  const collectionRate =
    totalRevenue > 0 ? (totalPaid / totalRevenue) * 100 : 0;

  // Calculate date range
  const dates = invoices.map((invoice) => new Date(invoice.generationDate));
  const minDate = new Date(Math.min(...dates.map((d) => d.getTime())));
  const maxDate = new Date(Math.max(...dates.map((d) => d.getTime())));

  return {
    totalInvoices,
    totalRevenue,
    outstandingAmount,
    collectionRate,
    dateRange: {
      from: minDate.toISOString().split("T")[0],
      to: maxDate.toISOString().split("T")[0],
    },
  };
}
