"use client";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { ScrollArea } from "@/components/ui/scroll-area";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { mockInvoices } from "@/data/mock-invoices";
import { mockJobs } from "@/data/mock-jobs";
import { formatCurrency } from "@/lib/currencyHelper";
import {
  getRelativeDateLabel,
  isDueSoon,
  isOverdue,
  isToday,
} from "@/lib/date-utils";
import getStatusName from "@/lib/enumHelper";
import {
  calculateInvoiceRiskLevel,
  getInvoiceStatusColor,
  getRiskLevelColor,
} from "@/lib/invoice-utils";
import { formatDuration } from "@/lib/job-utils";
import { formatDate } from "@/lib/utils";
import type { Client } from "@/types/client";
import {
  Banknote,
  Building2,
  Calendar,
  CheckCircle,
  Clock,
  CreditCard,
  FileText,
  Globe,
  Mail,
  MapPin,
  Phone,
  Shield,
  User,
  Users,
  XCircle,
} from "lucide-react";
import { useState } from "react";

interface ClientDetailsModalProps {
  isOpen: boolean;
  onClose: () => void;
  client: Client | null;
}

const getStatusBadgeVariant = (status: string) => {
  switch (status) {
    case "Completed":
    case "Delivered":
      return "default";
    case "In Progress":
      return "secondary";
    case "On Hold":
    case "Pending":
      return "outline";
    case "Cancelled":
      return "destructive";
    case "Under Review":
      return "secondary";
    case "Not Started":
      return "outline";
    default:
      return "outline";
  }
};

const formatServices = (services: string[] | undefined | null) => {
  if (!services || !Array.isArray(services) || services.length === 0)
    return "-";
  if (services.length === 1) return services[0];
  return `${services[0]} +${services.length - 1} more`;
};

const getServicesTitle = (services: string[] | undefined | null) => {
  if (!services || !Array.isArray(services) || services.length === 0)
    return "No services";
  return services.join(", ");
};

export default function ClientDetailsModal({
  isOpen,
  onClose,
  client,
}: ClientDetailsModalProps) {
  const [activeTab, setActiveTab] = useState("details");

  if (!client) return null;

  // Get recent jobs for this client (last 10)
  const clientJobs = mockJobs
    .filter((job) => job.clientId === client.id)
    .sort(
      (a, b) =>
        new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
    )
    .slice(0, 10);

  // Get recent invoices for this client (last 10)
  const clientInvoices = mockInvoices
    .filter((invoice) => invoice.clientId === client.id)
    .sort(
      (a, b) =>
        new Date(b.generationDate).getTime() -
        new Date(a.generationDate).getTime()
    )
    .slice(0, 10);

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="min-w-[80vw] lg:min-w-5xl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Building2 className="h-5 w-5" />
            Client Details - {client.name}
          </DialogTitle>
        </DialogHeader>
        <ScrollArea className="max-h-[70vh] pr-4">
          <Tabs
            value={activeTab}
            onValueChange={setActiveTab}
            className="w-full"
          >
            <TabsList className="grid w-full grid-cols-3">
              <TabsTrigger value="details">Client Details</TabsTrigger>
              <TabsTrigger value="jobs">
                Recent Jobs ({clientJobs.length})
              </TabsTrigger>
              <TabsTrigger value="invoices">
                Recent Invoices ({clientInvoices.length})
              </TabsTrigger>
            </TabsList>

            <TabsContent value="details" className="mt-4">
              <div className="space-y-6">
                {/* Header Information */}
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-lg">
                      <Building2 className="h-5 w-5" />
                      Basic Information
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <FileText className="h-4 w-4" />
                          SG Code
                        </div>
                        <div className="font-medium">{client.sgCode}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Building2 className="h-4 w-4" />
                          Client Name
                        </div>
                        <div className="font-medium">{client.name}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Building2 className="h-4 w-4" />
                          Company Name
                        </div>
                        <div>{client.companyName || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Banknote className="h-4 w-4" />
                          Currency
                        </div>
                        <div>
                          {client.currency ? (
                            <Badge
                              variant="outline"
                              className="text-xs font-mono"
                            >
                              {formatCurrency(client.currency)
                                .replace("0", "")
                                .trim()}
                            </Badge>
                          ) : (
                            "-"
                          )}
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>

                {/* Contact Information */}
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-lg">
                      <User className="h-5 w-5" />
                      Contact Information
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <User className="h-4 w-4" />
                          Contact Person
                        </div>
                        <div>{client.contactPerson || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Phone className="h-4 w-4" />
                          Phone Number
                        </div>
                        <div>{client.phone || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Mail className="h-4 w-4" />
                          Email
                        </div>
                        <div>{client.email || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Globe className="h-4 w-4" />
                          Website
                        </div>
                        <div>
                          {client.website ? (
                            <a
                              href={client.website}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="text-blue-600 hover:underline"
                            >
                              {client.website}
                            </a>
                          ) : (
                            "-"
                          )}
                        </div>
                      </div>
                    </div>

                    {client.address && (
                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <MapPin className="h-4 w-4" />
                          Address
                        </div>
                        <div className="text-sm">{client.address}</div>
                      </div>
                    )}
                  </CardContent>
                </Card>

                {/* Additional Contact */}
                {(client.anotherContactPerson || client.anotherEmail) && (
                  <Card>
                    <CardHeader>
                      <CardTitle className="flex items-center gap-2 text-lg">
                        <Users className="h-5 w-5" />
                        Additional Contact
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                          <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                            <User className="h-4 w-4" />
                            Another Contact Person
                          </div>
                          <div>{client.anotherContactPerson || "-"}</div>
                        </div>

                        <div className="space-y-2">
                          <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                            <Mail className="h-4 w-4" />
                            Another Email
                          </div>
                          <div>{client.anotherEmail || "-"}</div>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                )}

                {/* Business Information */}
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-lg">
                      <Building2 className="h-5 w-5" />
                      Business Information
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <MapPin className="h-4 w-4" />
                          Country
                        </div>
                        <div>{client.country || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <User className="h-4 w-4" />
                          Marketer
                        </div>
                        <div>{client.marketer?.fullName || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Building2 className="h-4 w-4" />
                          Type
                        </div>
                        <div>
                          {client.clientType ? (
                            <Badge variant="secondary" className="text-xs">
                              {client.clientType.name}
                            </Badge>
                          ) : (
                            "-"
                          )}
                        </div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Clock className="h-4 w-4" />
                          Deadline
                        </div>
                        <div>
                          {client.deadlineType ? (
                            <Badge variant="outline" className="text-xs">
                              {getStatusName([], client.deadlineType)}
                            </Badge>
                          ) : (
                            "-"
                          )}
                        </div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Shield className="h-4 w-4" />
                          VAT/TAX ID
                        </div>
                        <div>{client.vatId || "-"}</div>
                      </div>
                    </div>
                  </CardContent>
                </Card>

                {/* Payment Information */}
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-lg">
                      <CreditCard className="h-5 w-5" />
                      Payment Information
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Calendar className="h-4 w-4" />
                          Payment Cycle
                        </div>
                        <div>{client.paymentCycle || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <CreditCard className="h-4 w-4" />
                          Payment Method
                        </div>
                        <div>{client.paymentMethod?.name || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <FileText className="h-4 w-4" />
                          File Transfer
                        </div>
                        <div>{client.fileTransferMethod?.name || "-"}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <FileText className="h-4 w-4" />
                          File Backup
                        </div>
                        <div>{client.fileBackup || "-"}</div>
                      </div>
                    </div>
                  </CardContent>
                </Card>

                {/* Test Information */}
                {(client.folderId || client.lockDate) && (
                  <Card>
                    <CardHeader>
                      <CardTitle className="flex items-center gap-2 text-lg">
                        <FileText className="h-5 w-5" />
                        Test Information
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                          <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                            <FileText className="h-4 w-4" />
                            Test Folder
                          </div>
                          <div>{client.folder?.folderName || "-"}</div>
                        </div>

                        <div className="space-y-2">
                          <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                            <Calendar className="h-4 w-4" />
                            Lock Date
                          </div>
                          <div>{formatDate(client.lockDate)}</div>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                )}

                {/* Record Information */}
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-lg">
                      <Clock className="h-5 w-5" />
                      Record Information
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Calendar className="h-4 w-4" />
                          Created At
                        </div>
                        <div>{formatDate(client.createdAt)}</div>
                      </div>

                      <div className="space-y-2">
                        <div className="flex items-center gap-2 text-sm font-medium text-muted-foreground">
                          <Calendar className="h-4 w-4" />
                          Last Updated
                        </div>
                        <div>{formatDate(client.updatedAt)}</div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </TabsContent>

            <TabsContent value="jobs" className="mt-4">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <FileText className="h-5 w-5" />
                    Recent Jobs ({clientJobs.length})
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <ScrollArea className="max-h-[60vh]">
                    {clientJobs.length === 0 ? (
                      <div className="text-center py-8 text-muted-foreground">
                        No jobs found for this client
                      </div>
                    ) : (
                      <div className="overflow-x-auto">
                        <Table>
                          <TableHeader>
                            <TableRow>
                              <TableHead>Folder Name</TableHead>
                              <TableHead>Services</TableHead>
                              <TableHead>Job Type</TableHead>
                              <TableHead>Status</TableHead>
                              <TableHead>Input/Output</TableHead>
                              <TableHead>Delivery</TableHead>
                              <TableHead>Type</TableHead>
                              <TableHead>Total Time</TableHead>
                              <TableHead>Progress</TableHead>
                            </TableRow>
                          </TableHeader>
                          <TableBody>
                            {clientJobs.map((job) => (
                              <TableRow key={job.id}>
                                <TableCell className="font-medium max-w-[200px]">
                                  <div
                                    className="truncate"
                                    title={job.folderName || "No folder name"}
                                  >
                                    {job.folderName || "-"}
                                  </div>
                                </TableCell>
                                <TableCell>
                                  <div className="max-w-[150px]">
                                    <Badge
                                      variant="outline"
                                      className="text-xs"
                                      title={getServicesTitle(job.service)}
                                    >
                                      {formatServices(job.service)}
                                    </Badge>
                                  </div>
                                </TableCell>
                                <TableCell className="max-w-[150px]">
                                  <div
                                    className="truncate"
                                    title={job.jobType || "No job type"}
                                  >
                                    {job.jobType || "-"}
                                  </div>
                                </TableCell>
                                <TableCell>
                                  <Badge
                                    variant={getStatusBadgeVariant(
                                      job.statusDropdown
                                    )}
                                    className="text-xs"
                                  >
                                    {job.statusDropdown || "Unknown"}
                                  </Badge>
                                </TableCell>
                                <TableCell>
                                  <div className="flex flex-col text-sm">
                                    <span>In: {job.inputs || 0}</span>
                                    <span>Out: {job.output || 0}</span>
                                  </div>
                                </TableCell>
                                <TableCell>
                                  <div className="flex flex-col text-sm">
                                    <span
                                      className={`${
                                        isOverdue(job.delivery) &&
                                        job.statusDropdown !== "Completed"
                                          ? "text-red-600 font-medium"
                                          : ""
                                      }`}
                                    >
                                      {getRelativeDateLabel(job.delivery)}
                                    </span>
                                    <div className="flex items-center gap-1 text-xs text-muted-foreground">
                                      <Clock className="h-3 w-3" />
                                      {job.deliveryTime || "No time"}
                                      {isToday(job.delivery) && (
                                        <Badge
                                          variant="default"
                                          className="text-xs ml-1"
                                        >
                                          Today
                                        </Badge>
                                      )}
                                      {isOverdue(job.delivery) &&
                                        job.statusDropdown !== "Completed" && (
                                          <Badge
                                            variant="destructive"
                                            className="text-xs ml-1"
                                          >
                                            Overdue
                                          </Badge>
                                        )}
                                      {isDueSoon(job.delivery) &&
                                        job.statusDropdown !== "Completed" && (
                                          <Badge
                                            variant="outline"
                                            className="text-xs ml-1"
                                          >
                                            Due Soon
                                          </Badge>
                                        )}
                                    </div>
                                  </div>
                                </TableCell>
                                <TableCell>
                                  <Badge variant="default" className="text-xs">
                                    {job.delivery}
                                  </Badge>
                                </TableCell>
                                <TableCell className="font-mono text-sm">
                                  {formatDuration(job.totalTime || 0)}
                                </TableCell>
                                <TableCell>
                                  <div className="flex items-center gap-2">
                                    <div className="flex items-center gap-1">
                                      {job.proDone ? (
                                        <CheckCircle className="h-4 w-4 text-green-500" />
                                      ) : (
                                        <XCircle className="h-4 w-4 text-gray-400" />
                                      )}
                                      <span className="text-xs">Pro</span>
                                    </div>
                                    <div className="flex items-center gap-1">
                                      {job.qcDone ? (
                                        <CheckCircle className="h-4 w-4 text-green-500" />
                                      ) : (
                                        <XCircle className="h-4 w-4 text-gray-400" />
                                      )}
                                      <span className="text-xs">QC</span>
                                    </div>
                                  </div>
                                </TableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </div>
                    )}
                  </ScrollArea>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="invoices" className="mt-4">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <CreditCard className="h-5 w-5" />
                    Recent Invoices ({clientInvoices.length})
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <ScrollArea className="max-h-[60vh]">
                    {clientInvoices.length === 0 ? (
                      <div className="text-center py-8 text-muted-foreground">
                        No invoices found for this client
                      </div>
                    ) : (
                      <div className="overflow-x-auto">
                        <Table>
                          <TableHeader>
                            <TableRow>
                              <TableHead>Invoice ID</TableHead>
                              <TableHead>Generation Date</TableHead>
                              <TableHead>Due Date</TableHead>
                              <TableHead>Jobs</TableHead>
                              <TableHead>Amount</TableHead>
                              <TableHead>Status</TableHead>
                              <TableHead>Risk Level</TableHead>
                            </TableRow>
                          </TableHeader>
                          <TableBody>
                            {clientInvoices.map((invoice) => {
                              const riskLevel =
                                calculateInvoiceRiskLevel(invoice);
                              return (
                                <TableRow key={invoice.id}>
                                  <TableCell>
                                    <div className="font-medium text-blue-600">
                                      {invoice.invoiceNumber}
                                    </div>
                                  </TableCell>
                                  <TableCell>
                                    <div className="flex items-center gap-2">
                                      <Calendar className="h-4 w-4 text-muted-foreground" />
                                      <span className="text-sm">
                                        {/* {formatDate(invoice.generationDate)} */}
                                      </span>
                                    </div>
                                  </TableCell>
                                  <TableCell>
                                    <div className="flex items-center gap-2">
                                      <Calendar className="h-4 w-4 text-muted-foreground" />
                                      <span className="text-sm">
                                        {/* {formatDate(invoice.dueDate)} */}
                                      </span>
                                    </div>
                                  </TableCell>
                                  <TableCell>
                                    <div className="flex items-center gap-2">
                                      <FileText className="h-4 w-4 text-muted-foreground" />
                                      <span className="font-medium">
                                        {invoice.jobs.length}
                                      </span>
                                      <span className="text-sm text-muted-foreground">
                                        jobs
                                      </span>
                                    </div>
                                  </TableCell>
                                  <TableCell>
                                    <div className="font-medium">
                                      {formatCurrency(client.currency!)}
                                    </div>
                                    {invoice.status === "Partial Paid" && (
                                      <div className="text-xs text-muted-foreground">
                                        Paid: {formatCurrency(client.currency!)}
                                      </div>
                                    )}
                                  </TableCell>
                                  <TableCell>
                                    <Badge
                                      className={`${getInvoiceStatusColor(
                                        invoice.status
                                      )} font-medium`}
                                    >
                                      {invoice.status}
                                    </Badge>
                                  </TableCell>
                                  <TableCell>
                                    <Badge
                                      className={`${getRiskLevelColor(
                                        riskLevel
                                      )} font-medium capitalize`}
                                    >
                                      {riskLevel}
                                    </Badge>
                                  </TableCell>
                                </TableRow>
                              );
                            })}
                          </TableBody>
                        </Table>
                      </div>
                    )}
                  </ScrollArea>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
}
