"use client";

import type React from "react";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getDeadlines } from "@/lib/api/jobApi";
import { formatCurrency } from "@/lib/currencyHelper";
import getStatusName from "@/lib/enumHelper";
import type { Client, ClientSort } from "@/types/client";
import {
  ChevronDown,
  ChevronLeft,
  ChevronRight,
  ChevronsLeft,
  ChevronsRight,
  ChevronUp,
  Edit,
  MoreHorizontal,
  Trash2,
} from "lucide-react";

interface ClientTableProps {
  clients: Client[];
  sort: ClientSort;
  onSort: (field: keyof Client) => void;
  onEdit: (client: Client) => void;
  onDelete: (clientId: string) => void;
  currentPage: number;
  totalPages: number;
  onPageChange: (page: number) => void;
  totalItems: number;
  itemsPerPage: number;
  onViewDetails: (client: Client) => void;
}

export default function ClientTable({
  clients,
  sort,
  onSort,
  onEdit,
  onDelete,
  currentPage,
  totalPages,
  onPageChange,
  totalItems,
  itemsPerPage,
  onViewDetails,
}: ClientTableProps) {
  const SortableHeader = ({
    field,
    children,
  }: {
    field: keyof Client;
    children: React.ReactNode;
  }) => (
    <TableHead>
      <Button
        variant="ghost"
        size="sm"
        className="h-auto p-0 font-semibold hover:bg-transparent"
        onClick={() => onSort(field)}
      >
        {children}
        {sort.field === field &&
          (sort.direction === "asc" ? (
            <ChevronUp className="ml-1 h-4 w-4" />
          ) : (
            <ChevronDown className="ml-1 h-4 w-4" />
          ))}
      </Button>
    </TableHead>
  );

  const { data: deadlines } = useSkillQuery({
    queryKey: ["deadlines"],
    queryFn: () => getDeadlines(),
  });

  const handleRowClick = (client: Client, event: React.MouseEvent) => {
    // Prevent row click when clicking on action buttons
    const target = event.target as HTMLElement;
    if (target.closest("[data-prevent-row-click]")) {
      return;
    }
    onViewDetails(client);
  };

  const startItem = (currentPage - 1) * itemsPerPage + 1;
  const endItem = Math.min(currentPage * itemsPerPage, totalItems);

  return (
    <div className="space-y-4">
      <Card className="p-0">
        <CardContent className="p-0">
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <SortableHeader field="sgCode">SG Code</SortableHeader>
                  <SortableHeader field="name">Client Name</SortableHeader>
                  <SortableHeader field="companyName">
                    Company Name
                  </SortableHeader>
                  <SortableHeader field="contactPerson">
                    Contact Person
                  </SortableHeader>
                  <SortableHeader field="email">Email</SortableHeader>
                  <SortableHeader field="currency">Currency</SortableHeader>
                  <SortableHeader field="country">Country</SortableHeader>
                  <SortableHeader field="marketer">Marketer</SortableHeader>
                  <SortableHeader field="clientType">Type</SortableHeader>
                  <SortableHeader field="deadlineType">Deadline</SortableHeader>
                  <TableHead className="w-[50px]">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {clients.length === 0 ? (
                  <TableRow>
                    <TableCell
                      colSpan={11}
                      className="text-center py-8 text-muted-foreground"
                    >
                      No clients found
                    </TableCell>
                  </TableRow>
                ) : (
                  clients.map((client) => (
                    <TableRow
                      key={client.id}
                      className="cursor-pointer hover:bg-muted/50"
                      onClick={(e) => handleRowClick(client, e)}
                    >
                      <TableCell className="font-medium">
                        {client.sgCode}
                      </TableCell>
                      <TableCell className="font-medium">
                        {client.name}
                      </TableCell>
                      <TableCell>{client.companyName || "-"}</TableCell>
                      <TableCell>{client.contactPerson || "-"}</TableCell>
                      <TableCell>{client.email || "-"}</TableCell>
                      <TableCell>
                        {client.currency && (
                          <Badge
                            variant="outline"
                            className="text-xs font-mono"
                          >
                            {formatCurrency(client.currency)}
                          </Badge>
                        )}
                      </TableCell>
                      <TableCell>{client.country || "-"}</TableCell>
                      <TableCell>{client.marketer?.fullName || "-"}</TableCell>
                      <TableCell>
                        {client.clientTypeId && (
                          <Badge variant="secondary" className="text-xs">
                            {client.clientType?.name}
                          </Badge>
                        )}
                      </TableCell>
                      <TableCell>
                        <Badge variant="outline" className="text-xs">
                          {getStatusName(deadlines?.data, client.deadlineType)}
                        </Badge>
                      </TableCell>
                      <TableCell data-prevent-row-click>
                        <DropdownMenu>
                          <DropdownMenuTrigger asChild>
                            <Button
                              variant="ghost"
                              size="sm"
                              className="h-8 w-8 p-0"
                            >
                              <MoreHorizontal className="h-4 w-4" />
                              <span className="sr-only">Open menu</span>
                            </Button>
                          </DropdownMenuTrigger>
                          <DropdownMenuContent align="end">
                            <DropdownMenuItem onClick={() => onEdit(client)}>
                              <Edit className="mr-2 h-4 w-4" />
                              Edit
                            </DropdownMenuItem>
                            <DropdownMenuItem
                              onClick={() => onDelete(client.id)}
                              className="text-destructive"
                            >
                              <Trash2 className="mr-2 h-4 w-4" />
                              Delete
                            </DropdownMenuItem>
                          </DropdownMenuContent>
                        </DropdownMenu>
                      </TableCell>
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="flex items-center justify-between">
          <div className="text-sm text-muted-foreground">
            Showing {startItem} to {endItem} of {totalItems} clients
          </div>
          <div className="flex items-center space-x-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(1)}
              disabled={currentPage === 1}
            >
              <ChevronsLeft className="h-4 w-4" />
              <span className="sr-only">First page</span>
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(currentPage - 1)}
              disabled={currentPage === 1}
            >
              <ChevronLeft className="h-4 w-4" />
              <span className="sr-only">Previous page</span>
            </Button>
            <div className="flex items-center space-x-1">
              {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                const pageNumber =
                  Math.max(1, Math.min(totalPages - 4, currentPage - 2)) + i;
                return (
                  <Button
                    key={pageNumber}
                    variant={currentPage === pageNumber ? "default" : "outline"}
                    size="sm"
                    onClick={() => onPageChange(pageNumber)}
                    className="w-8 h-8 p-0"
                  >
                    {pageNumber}
                  </Button>
                );
              })}
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              <ChevronRight className="h-4 w-4" />
              <span className="sr-only">Next page</span>
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(totalPages)}
              disabled={currentPage === totalPages}
            >
              <ChevronsRight className="h-4 w-4" />
              <span className="sr-only">Last page</span>
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
