export function calculateTotalTime(
  job: Partial<{
    incoming: string;
    actualTime: string;
    scriptAmount: number;
    service: string;
  }>
): number {
  // Formula: Total Time = (Actual Time - Incoming Time) + (Script Amount / 100) * 2
  // This is a simplified version - adjust based on your actual Google Sheet formula

  if (!job.incoming || !job.actualTime || !job.scriptAmount) {
    return 0;
  }

  const incomingDate = new Date(job.incoming);
  const actualDate = new Date(job.actualTime);

  // Calculate hours difference
  const timeDiffHours =
    (actualDate.getTime() - incomingDate.getTime()) / (1000 * 60 * 60);

  // Add script amount factor (example formula)
  const scriptFactor = (job.scriptAmount / 100) * 2;

  return Math.max(0, Math.round((timeDiffHours + scriptFactor) * 100) / 100);
}

export function formatDateTime(dateString: string): string {
  if (!dateString) return "";
  return new Date(dateString).toLocaleString();
}

export function formatDuration(hours: number): string {
  if (hours === 0) return "0 hours";

  const wholeHours = Math.floor(hours);
  const minutes = Math.round((hours - wholeHours) * 60);

  if (wholeHours === 0) {
    return `${minutes} minutes`;
  } else if (minutes === 0) {
    return `${wholeHours} hour${wholeHours !== 1 ? "s" : ""}`;
  } else {
    return `${wholeHours}h ${minutes}m`;
  }
}

export function formatDate(dateString?: string): string {
  if (!dateString) return "-";
  return new Date(dateString).toLocaleDateString();
}
